import React from 'react';
import classes from './Input.css';

const input = (props) => {
  let inputElement = null;

  let inputClasses = [classes.InputElement];

  if(props.invalid && props.validation && props.touched){
    inputClasses.push(classes.Invalid);
  }

  switch(props.elementType){
    case('input'):
      inputElement = <input className={inputClasses.join(' ')} {...props.elementConfig} onChange={props.changed}/>;
      break;
    case('textarea'):
      inputElement = <textarea className={inputClasses.join(' ')} {...props.elementConfig} onChange={props.changed}/>;
      break;
    case('select'):
      inputElement = (
        <select className={inputClasses.join(' ')} onChange={props.changed}>
          {props.elementConfig.options.map(option => {
            return (
              <option key={option.value}
                value={option.value}>
                {option.displayValue}
              </option>
          )
          })}
        </select>
      );
      break;
    default:
      inputElement = <input className={inputClasses.join(' ')} {...props.elementConfig} onChange={props.changed}/>;
  }

  return (
    <div className={classes.Input}>
      <label className={classes.Label}>{props.label}</label>
      {inputElement}
    </div>
  );
}

export default input;



